    margin-top: 100px
  }
}

@media only screen and (max-width:991.5px) {
  #profilePhoto {
    max-width: 720px;
    max-height: 720px
  }
  .headingLinks {
    left: 1px;
    width: 102%
  }
}

@media only screen and (max-width:767.5px) {
  #profilePhoto {
    max-width: 540px;
    max-height: 540px
  }
  .headingLinks {
    left: 0;
    width: 100%
  }
  #info {
    padding-right: 0px;
  }
}

@media only screen and (max-width:575.5px) {
  #profilePhoto {
    max-width: 104%;
    max-height: none;
    overflow: hidden;
  }
  .headingLinks {
    left: 0;
    width: 100%
  }
  .headingLinks .col-sm-2 {
    width: 20%
  }
}

.firstEducation {
  margin-top: 40px
}

.leftEducation {
  margin-right: 20px
}

.leftEducation, .rightEducation {
  max-height: 140px;
  background-color: #fff;
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 535px
}

.rightEducation {
  margin-top: 100px;
  margin-left: 20px
}

.circle-left {
  left: -27px
}

.circle-left, .circle-right {
  height: 15px;
  width: 15px;
  background-color: #355c7d;
  border-radius: 50%;
  position: absolute;
  z-index: -1;
  top: 19px
}

.circle-right {
  right: -27px
}

.triangle-left {
  border-right: 19px solid #fff;
  left: -19px
}

.triangle-left, .triangle-right {
  width: 0;
  height: 0;
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  position: absolute
}

.triangle-right {
  border-left: 19px solid #fff;
  right: -19px
}

.lastEducation {
  margin-bottom: 50px
}

.milan {
  position: absolute;
  top: 250px;
}

.copenhagen {
  position: absolute;
  top: 235px;
  right: 16px;
}

.padua {
  position: absolute;
  right: 16px;
  top: 70px;
}

.future {
  position: absolute;
  top: 375px;
}

.future2 {
  position: absolute;
  top: 235px;
  right: 16px;
}

.shadow {
  border-radius: 5px;
}

.imgDiv, #profilePhoto {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.headingLinks {
  border-bottom-right-radius: 5px;
}

.work {
  position: absolute;
  top: 1635px;
}

@media only screen and (max-width:1199px) {
  .leftEducation, .rightEducation {
    max-width: 445px
  }
  .imgDiv, #profilePhoto {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 0px;
  }
  .headingLinks {
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  .work {
    top: 1690px;
  }
}

@media only screen and (max-width:991.5px) {
  .top90 {
    margin-top: 120px;
  }
  .leftEducation, .rightEducation {
    max-width: 325px
  }
  .doubleLine {
    max-height: 155px
  }
  .h6, h6 {
    font-size: 0.8rem;
  }
  .h5, h5 {
    font-size: 1.0rem;
  }
  .shadow {
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
  .imgDiv, #profilePhoto {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 0px;
    border-top-right-radius: 5px;
  }
  .headingLinks {
    border-bottom-right-radius: 5px;
  }
  .work {
    top: 2475px;
  }
}

@media only screen and (max-width:767.5px) {
  .leftEducation, .rightEducation {
    max-width: 235px
  }
  .doubleLine {
    max-height: 155px
  }
  .work {
    top: 2435px;
  }
}

@media only screen and (max-width:575.5px) {
  .circle-left, .circle-right, .triangle-left, .triangle-right {
    display: none
  }
  .leftEducation, .rightEducation {
    max-width: none;
    margin-left: 0;
    margin-right: 0;
    margin-top: 20px
  }
  .h6, h6 {
    font-size: 1.0rem;
  }
  .h5, h5 {
    font-size: 1.25rem;
  }
  .imgDiv, #profilePhoto, .headingLinks {
    border-radius: 0px;
  }
  .padua, .milan, .future, .copenhagen {
    position: inherit;
  }
  #info {
    padding-right: 0px;
  }
  .work {
    top: 2870px;
  }
  .future2 {
    position: absolute;
    top: 515px;
    right: 0px;
    position: inherit;
  }
}

.floatLeft {
  float: left;
}

.skillsDiv, .milestone {
  border-radius: 5px !important;
}

.triggerNotification{
  display: none !important;
}